html {
  font-family: Arial, Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.wrapper {
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  gap: 40px 30px;
  max-width: 500px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    max-width: 1000px;
  }
}

.person {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

